.cart-item-container {
    width: 100%;
    display: flex;
    height: 80px;
    margin-bottom: 15px;
    align-items: center;

    img {
        width: 35%;
        height: 95%;
    }

    .item-details {
        width: 70%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        padding: 10px 20px;

        .name {
            margin:5px;
            font-size: 14px;
        }
    }
}